.fen-cover{
  content: '';
  display: block;
  width: 100%;
  height: 100vh;
  position: fixed;
  text-shadow: 0 .05rem .1rem rgba(0, 0, 0, .5);
  box-shadow: inset 0 0 5rem rgba(0, 0, 0, .5);
  background-color: #24282a!important;
}

.fen-cover *{
  color: #fff;
}


.fen-hero{
  /*position: fixed;*/
  text-align: center;
}
.fen-hero h2{
  font-size: 72px;
  font-weight: 700;
  height: 100vh;
  line-height: 100vh;
}

@media(max-width: 765px){
  .fen-hero h2{
    font-size: 50px;
    font-weight: 700;
    height: 100vh;
    line-height: 100vh;
  }
}

@media(max-width: 568px){
  .fen-hero h2{
    font-size: 20px;
    font-weight: 700;
    height: 100vh;
    line-height: 100vh;
  }
}

.fen-foo{
  position: fixed;
  display: block;
  bottom: 20px;
  width: 100%;
}
.fen-foo p{
  color: #959595;
  font-size: 14px;
  margin: 0 auto;
  text-align: center;
}

.fen-foo a:{
  color: #fff;
}
.fen-foo a:hover{
  color: #fff;
}